import { slice, concat } from 'lodash';

import clsx from 'clsx';
import React, { useState, useEffect } from 'react';
import { useInView } from 'react-intersection-observer';

import data from '../../data/data.json';
import { useMode } from '../../providers/mode';
import ItemsLoader from '../loaders/ItemsLoader';
import Item from './Item';
import Tabs from './Tabs';

import { ReactComponent as ArrowDown } from '../../assets/icons/arrow-down.svg';

const tabsList = ['ETH', 'BSC', 'Solana',  'Matic', 'FTM',    'AVAX',  'Terra', 'Ardbitrum', 'Celo', 'HECO','xDai', 'Tezos', 'Harmony', 'Kucoin', 'Moonriver'];

const DATA = data;
const LENGTH = data.length;
const LIMIT = 8;

export default function Items() {
    const { ref, inView } = useInView({ threshold: 0 });

    const [current, setCurrent] = useState(0);
    const [items, setItems] = useState(slice(DATA, 0, LIMIT));
    const [isMoreDataAvailable, setIsMoreDataAvailable] = useState(false);
    const [arrayIndex, setArrayIndex] = useState(LIMIT);
    const [isLoading, setIsLoading] = useState(false);
    const { isDark } = useMode();

    useEffect(() => {
        items.length < LENGTH ? setIsMoreDataAvailable(true) : setIsMoreDataAvailable(false);
    }, [items]);

    useEffect(() => {
        const loadMore = () => {
            setIsLoading(true);
            setTimeout(() => {
                const newArrayIndex = arrayIndex + LIMIT;
                const newItems = concat(items, slice(DATA, arrayIndex, newArrayIndex));
                setArrayIndex(newArrayIndex);
                setItems(newItems);
                setIsLoading(false);
            }, 1000);
        };

        isMoreDataAvailable && inView && loadMore();
    }, [isMoreDataAvailable, inView, arrayIndex, items]);

    const lastId = items.length && items[items.length - 1].id;

    return (
        <>
            <Tabs data={tabsList} current={current} setCurrent={setCurrent} />
            <div className={clsx('items', isDark && 'items-dark')}>
                <div className="items-titles">
                    <div className={clsx('item-text', isDark && 'item-text-dark')}>Name</div>
                    <div className={clsx('item-text', isDark && 'item-text-dark')}>Chain</div>
                    <div className={clsx('item-text', isDark && 'item-text-dark')}>
                        24h users <ArrowDown />
                    </div>
                    <div className={clsx('item-text', isDark && 'item-text-dark')}>Reward Tokens</div>
                    <div className={clsx('item-text', isDark && 'item-text-dark')}>
                        TVL <ArrowDown />
                    </div>
                    <div className={clsx('item-text', isDark && 'item-text-dark')}>Info</div>
                </div>
                {!!items.length &&
                    items.map((item, index) => (
                        <Item
                            key={index}
                            ref={item.id === lastId ? ref : undefined}
                            index={index}
                            name={item.name}
                            logo={item.logo}
                            chains={item.chains}
                            users={item.users}
                            reward_tokens={item.reward_tokens}
                            tvl={item.tvl}
                            website={item.website}
                        />
                    ))}
                {isLoading && isMoreDataAvailable && <ItemsLoader />}
            </div>
        </>
    );
}
