import clsx from 'clsx';
import React from 'react';

import { useMode } from '../../providers/mode';

export default function Tabs({ data, current, setCurrent }) {
    const { isDark } = useMode();

    return (
        <div className="tabs">
            {data.map((tab, index) => (
                <button
                    key={index}
                    type="button"
                    onClick={() => setCurrent(index)}
                    className={clsx(
                        'btn-tab',
                        isDark && 'btn-tab-dark',
                        index === current && 'btn-tab-active',
                        isDark && index === current && 'btn-tab-active-dark'
                    )}
                >
                    {tab}
                </button>
            ))}
        </div>
    );
}
