import clsx from 'clsx';
import React, { useEffect, useRef } from 'react';

import { useMode } from '../../providers/mode';

import { ReactComponent as Arrow } from '../../assets/icons/arrow.svg';

const Item = React.memo(
    React.forwardRef(({ index, name, logo, chains, users, reward_tokens, tvl, website }, forwardedRef) => {
        const ref = useRef();
        const { isDark } = useMode();

        useEffect(() => {
            forwardedRef && forwardedRef(ref.current);
            return () => forwardedRef && forwardedRef(undefined);
        }, [forwardedRef]);

        return (
            <div className={clsx('item', isDark && 'item-dark')} ref={ref}>
                <div className={clsx('item-text item-name', isDark && 'item-text-dark')}>
                    <span className="item-index">{index + 1}</span>{' '}
                    <span className="item-logo" style={{ backgroundImage: `url(${logo})` }}></span> {name}
                </div>
                <div className={clsx('item-text', isDark && 'item-text-dark')}>
                    {!!chains.length &&
                        chains.map((chain, ind) => (
                            <span key={ind} className="item-chain" style={{ backgroundImage: `url(${chain})` }}></span>
                        ))}
                </div>
                <div className={clsx('item-text', isDark && 'item-text-dark')}>{users}</div>
                <div className={clsx('item-text', isDark && 'item-text-dark')}>{reward_tokens}</div>
                <div className={clsx('item-text', isDark && 'item-text-dark')}>${tvl}b</div>
                <div className={clsx('item-text', isDark && 'item-text-dark')}>
                    <a
                        href={website}
                        className={clsx('item-btn', isDark && 'item-btn-dark')}
                        target="_blank"
                        rel="noopener noreferrer"
                    >
                        <span>
                            Website
                            <Arrow />
                        </span>
                    </a>
                </div>
            </div>
        );
    })
);

Item.displayName = 'Item';

export default Item;
